import Swiper from 'swiper';
import * as breakpoints from '~global-js/helpers/breakpoints';

export default function carouselPropertySwiper() {
  const itemCount = document.querySelectorAll('.carousel-properties__item').length;

  if (itemCount > 0) {
    const shouldBeSwipe = (breakpoints.isSmallScreen() && itemCount > 1)
      || (breakpoints.isMediumScreen() && itemCount > 2)
      || (breakpoints.isLargeScreen() && itemCount > 3);

    if (shouldBeSwipe) {
      /* eslint-disable no-new */
      new Swiper('.carousel-container', {
        direction: 'horizontal',
        slidesPerView: 'auto',
        nextButton: '.carousel-properties__controller--next',
        prevButton: '.carousel-properties__controller--prev',
        slidesOffsetAfter: 0,
        simulateTouch: false,
      });
      /* eslint-enable no-new */
    } else {
      document.querySelector('.carousel-properties__slider').classList.add('disable-slide');
    }
  }
}



